.popup-card {
    border-radius: 16px;
    margin: 0 auto;
    width: 100%;
    max-width: 100%;
    min-height: 500px;
    overflow: hidden;
    object-fit: cover;
    background-size: cover;
}

.popup-info {
    position: relative;
    width: 100%;
    height: 500px;
    background-color: rgb(233, 233, 233);
    transform: translateY(100%) translateY(-88px) translateZ(0);
    transition: transform 0.5s ease-out;
}

.popup-info:before {
    z-index: -1;
    display: block;
    position: absolute;
    content: ' ';
    width: 100%;
    height: 100%;
    transform: translateY(-100%) translateY(88px) translateZ(0);
    transition: transform 0.5s ease-out;
}

.popup-card:hover .popup-info,
.popup-card:hover .popup-info:before {
    transform: translateY(0) translateZ(0);
}

