.tilt-cards-container:nth-child(1n)  {
    background: linear-gradient(110deg, #fdbb2d 0%, #3a1c71 100%);
    box-shadow: 0px 5px 20px -10px #3a1c71;
}

.tilt-cards-container:nth-child(1n)  .card__type {
    background-color: #c08a53;
}

.tilt-cards-container:nth-child(3n),
.tilt-cards-container:nth-child(3n+4) {
    background: linear-gradient(90deg, rgba(255, 222, 0, 1) 34%, rgba(232, 255, 153, 1) 83%);
}

.tilt-cards-container:nth-child(3n) .card__type,
.tilt-cards-container:nth-child(3n + 4) .card__type {
    background-color: #000;
}

.tilt-cards-container:nth-child(2n) {
    background: linear-gradient(120deg, #1cb5e0 0%, #000851 100%);
    box-shadow: 0px 5px 20px -10px #000851;
}

.tilt-cards-container:nth-child(2n) .card__type {
    background-color: #1cb5e0;
}

.tilt-cards-container:nth-child(4n) {
    background: linear-gradient(0deg, rgba(199, 24, 0, 1) 10%, rgba(252, 194, 69, 1) 100%);
}

.tilt-cards-container:nth-child(4n+0) .card__type {
    background-color: rgba(199, 24, 0, 1);
}

.tilt-cards-container:nth-child(1),
.tilt-cards-container:nth-child(4n) {
    transform: translateY(0px) rotate(4deg);
}

.tilt-cards-container:nth-child(2n),
.tilt-cards-container:nth-child(3n){
    transform: translateY(0px) rotate(-4deg);
}

.tilt-cards-container:hover {
    z-index: 5;
    box-shadow: 0px 13px 30px -15px #000;
    transform: translateY(-10px);
}

@media screen and (max-width: 640px) {
    .tilt-cards-container {
        transform: translateY(0px) rotate(0deg) !important;
        width: 90%;
        margin: 50px auto 10px;
    }
}
* {
    box-sizing: border-box;
}

.gridywrap div[class*=gridy] {
    background: #eee;
    box-shadow: inset 0 0 0 1px #ddd;
    float: left;
    padding: 15px;
    position: relative;
}

.gridywrap .gridytest {
    overflow: hidden;
    height: 320px;
    width: 320px;
    width: 100%;
}

/*styling*/
.gridywrap .gridimg {
    background-position: center center;
    background-size: cover;
    bottom: 0;
    left: 0;
    overflow: hidden;
    position: absolute;
    right: 0;
    top: 0;
}

.gridywrap .gridimg img {
    width: 100%;
}

.gridywrap .gridinfo {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.4);
    text-align: center;
    transition: all 0.8s;
}

.gridywrap .gridinfo h3 {
    font-size: 24px;
    font-weight: bold;
    text-transform: uppercase;
    margin: 130px 0 20px;
    position: relative;
    opacity: 1;
}

.gridywrap .gridinfo h3:after {
    content: "";
    width: 25px;
    border-top: 4px solid #fff;
    display: block;
    margin: 0 auto;
    margin-top: 25px;
}

.gridywrap .gridinfo .gridmeta {
    transition-delay: 0.25s;
}

.gridywrap .gridinfo .gridmeta p {
    display: inline-block;
    font-size: 13px;
    margin-bottom: 15px;
}

.gridywrap .gridinfo p {
    width: 60%;
    margin: 0 auto;
    line-height: 22px;
    margin-bottom: 25px;
    transition-delay: 0.5s;
}

.gridywrap .gridinfo * {
    opacity: 0;
    transition: all 0.8s;
}

.gridywrap .gridinfo:hover * {
    opacity: 0.7;
}

.gridywrap .gridinfo:hover {
    background: rgba(0, 0, 0, 0.7);
}

.gridywrap .gridinfo:hover h3 {
    margin: 50px auto 0px ;
