.photo-figure {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 95vh;
}

.photo-figure-container {
    position: relative;
    text-align: center;
    text-transform: uppercase;
    cursor: none;
}

.figure-container-text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.figure-container-text h3 {
    font-weight: 700;
    font-size: 20px;
}

.figure-container-text p {
    font-size: 20px;
    font-weight: 600;
}

.figure-container-text h3,
.figure-container-text p {
    color: inherit;
}


.photo-figure img {
    width: 100vmin;
    height: 100vmin;
    border-radius: 3px;
}


figure:nth-child(1n) img,
figure:nth-child(1n+10) img {
    transform: scale(0.6) rotate(-12.5deg);
}

figure:nth-child(2n) img,
figure:nth-child(2n+10) img{
    transform: scale(0.6) rotate(-9.7222222222deg);
}

figure:nth-child(3n) img,
figure:nth-child(3n+10) img{
    transform: scale(0.6) rotate(-6.9444444444deg);
}

figure:nth-child(4n) img,
figure:nth-child(4n+10) img{
    transform: scale(0.6) rotate(-4.1666666667deg);
}

figure:nth-child(5n) img,
figure:nth-child(5n+10) img{
    transform: scale(0.6) rotate(-1.3888888889deg);
}

figure:nth-child(6n) img,
figure:nth-child(6n+10) img{
    transform: scale(0.6) rotate(1.3888888889deg);
}

figure:nth-child(7n) img,
figure:nth-child(7n+10) img{
    transform: scale(0.6) rotate(4.1666666667deg);
}

figure:nth-child(8n) img,
figure:nth-child(8n+10) img{
    transform: scale(0.6) rotate(6.9444444444deg);
}

figure:nth-child(9n) img,
figure:nth-child(9n+10) img{
    transform: scale(0.6) rotate(9.7222222222deg);
}

figure:nth-child(10n) img,
figure:nth-child(10+10n) img{
    transform: scale(0.6) rotate(12.5deg);
}

@media screen and (max-width: 1080px) {
    .photo-figure {
        height: 90vh
    }    
}

@media screen and (max-width: 640px) {
    .photo-figure {
        height: 75vh
    }    
}
